html {
  font-size: calc(100vw / 144);
  box-sizing: border-box;
  backface-visibility: hidden;

  @media (min-width: 2560px) {
    font-size: calc(2560px / 144);
  }
}

body,
html {
  // scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@function calc-between-width($width-start,
  $value-start,
  $width-end,
  $value-end) {
  @return calc(#{$value-start} * 1px + (#{$value-end} - #{$value-start}) * (100vw - #{$width-start} * 1px) / #{$width-start});
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-style: normal;
  line-height: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 1.6rem;
  font-weight: 400;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  // overflow: hidden; //     position: sticky -   
  background-color: var(--white-color);
}

.main-container {
  max-width: 2560px !important;
  width: 100%;
  margin: 0 auto;

  &--left {
    @media (min-width: 2560px) {
      max-width: unset !important;
      margin: unset;
      padding-left: calc((100% - 2560px) / 2);
    }
  }

  &--right {
    @media (min-width: 2560px) {
      max-width: unset !important;
      margin: unset;
      padding-right: calc((100% - 2560px) / 2);
    }
  }
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  @include mobile {
    padding: 0 16px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.main {
  position: relative;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.swiper-slide {
  width: auto;
}

.btn {
  display: inline-block;
  padding: 16px 32px;
  border-radius: 4px;

  font-family: "PP Neue Montreal";
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.44px;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in, color 0.3s ease-in;
  color: var(--black-brand-color);

  @media (min-width: 1440px) {
    font-size: 1.2rem;
    padding: 1.6rem 3.2rem;
    border-radius: 0.4rem;
    letter-spacing: 0.144rem;
  }

  @include mobile {
    padding: 16px;
  }

  &--fill {
    background-color: var(--focus-color);

    &:focus {
      background-color: var(--focus-active-color);
    }

    @include hover {
      background-color: var(--focus-active-color);
    }

    &:active {
      background-color: var(--focus-active-color);
    }
  }

  &--stroke {
    border: 1px solid var(--focus-color);

    &:focus {
      background-color: var(--focus-active-color);
    }

    @include hover {
      background-color: var(--focus-active-color);
    }

    &:active {
      background-color: var(--focus-active-color);
    }
  }

  &--like {
    width: 46px;
    height: 46px;
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    @media (min-width: 1440px) {
      width: 4.6rem;
      height: 4.6rem;
    }

    svg {
      width: 24px;
      height: 24px;
      stroke-width: 2px;
      fill: none;
      stroke: var(--white-color);
      transition: stroke 0.3s ease-in;
      pointer-events: none;

      @media (min-width: 1440px) {
        width: 2.4rem;
        height: 2.4rem;
      }
    }

    &:focus {
      svg {
        stroke: var(--focus-color);
      }
    }

    &:focus {
      background-color: unset;
    }

    @include hover {
      background-color: unset;

      svg {
        stroke: var(--focus-color);
      }
    }

    &:active {
      background-color: unset;

      svg {
        fill: var(--focus-active-color);
        stroke: var(--focus-active-color);
      }
    }

    &-dark {
      svg {
        stroke: var(--black-brand-color);
      }
    }
  }

  &--share {
    width: 46px;
    height: 46px;
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    @media (min-width: 1440px) {
      width: 4.6rem;
      height: 4.6rem;
    }

    svg {
      width: 16px;
      height: 16px;
      stroke-width: 2px;
      fill: none;
      fill: var(--black-brand-color);
      transition: stroke 0.3s ease-in;
      pointer-events: none;

      @media (min-width: 1440px) {
        width: 1.6rem;
        height: 1.6rem;
      }
    }

    &:focus {
      svg {
        fill: var(--focus-color);
      }
    }

    &:focus {
      background-color: unset;
    }

    @include hover {
      background-color: unset;

      svg {
        fill: var(--focus-color);
      }
    }

    &:active {
      background-color: unset;

      svg {
        fill: var(--focus-active-color);
      }
    }

    &-dark {
      svg {
        stroke: var(--black-brand-color);
      }
    }
  }

  &--fullscreen {
    width: 40px;
    height: 40px;
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    @media (min-width: 1440px) {
      width: 4rem;
      height: 4rem;
    }

    svg {
      width: 24px;
      height: 24px;
      stroke-width: 2px;
      fill: none;
      fill: var(--black-brand-color);
      transition: stroke 0.3s ease-in;
      pointer-events: none;
      fill: none;
      stroke: var(--black-brand-color);
      stroke-width: 1.125px;

      @media (min-width: 1440px) {
        width: 2.4rem;
        height: 2.4rem;
      }
    }

    &:focus {
      svg {
        stroke: var(--focus-color);
      }
    }

    &:focus {
      background-color: unset;
    }

    @include hover {
      background-color: unset;
      outline: 1px solid var(--focus-color);

      svg {
        fill: none;
        stroke: var(--focus-color);
      }
    }

    &:active {
      background-color: unset;

      svg {
        stroke: var(--focus-active-color);
      }
    }

    &-dark {
      svg {
        stroke: var(--black-brand-color);
      }
    }
  }

  &--print {
    display: flex;
    align-items: center;
    padding: 11px 32px;

    svg {
      width: 18px;
      height: 18px;
      margin-right: 10px;

      @media (min-width: 1441px) {
        width: 1.8rem;
        height: 1.8rem;
        margin-right: 1rem;
      }

      @include mobile {
        margin-right: 0;
      }
    }

    span {
      @include mobile {
        display: none;
      }
    }

    @media (min-width: 1441px) {
      padding: 1.1rem 3.2rem;
    }
  }

  &.active {
    svg {
      fill: var(--focus-color);
      stroke: var(--focus-color);
    }

    @include hover {
      fill: var(--focus-active-color);
    }
  }
}

.slider-control__btn {
  cursor: pointer;
  width: 48px;
  height: 48px;
  padding: 0 13.5px;
  border: 1px solid rgba(255, 150, 92, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 10;
  top: 50%;
  transform: translateY(-50%);

  &--prev {
    left: 30px;

    img {
      transform: rotate(180deg);
    }
  }

  &--next {
    right: 30px;
  }

  &.swiper-button-disabled {
    opacity: 0.4;
    cursor: default;
  }
}

.contact__btns {
  position: fixed;
  z-index: 10;
  bottom: 30px;
  right: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: 1440px) {
    bottom: 3rem;
    right: 3rem;
  }
}

.contact__btn {
  display: block;
  width: 48px;
  height: 48px;
  background-position: center center;
  background-size: 36px 36px;
  background-repeat: no-repeat;
  cursor: pointer;

  &:not(:last-child) {
    margin-bottom: 8px;
  }

  @media (min-width: 1441px) {
    width: 4.8rem;
    height: 4.8rem;
    background-size: 3.6rem 3.6rem;
  }
}

.phone-btn {
  background-image: url("./../img/call-phone-icon.svg");
  background-size: 30px 30px;

  @media (min-width: 1441px) {
    background-size: 3rem 3rem;
  }
}

.telegram-btn {
  background-image: url("./../img/mobile-tap/tap-6.svg");
}

.whatsapp-btn {
  background-image: url("./../img/mobile-tap/tap-5.svg");
  background-size: 30px 30px;

  @media (min-width: 1441px) {
    background-size: 3rem 3rem;
  }
}

.up-page-btn {
  display: none;
  opacity: 0;
  cursor: pointer;
  width: 42px;
  height: 42px;
  border: 1px solid var(--focus-color);
  border-radius: 4px;
  z-index: 8;
  position: fixed;
  bottom: 30px;
  left: 30px;
  background-image: url("./../img/arrow-up.svg");
  background-position: center center;
  background-size: 30px 30px;
  background-repeat: no-repeat;
  transition: opacity 0.2s ease-in;

  @media (min-width: 1440px) {
    width: 4.2rem;
    height: 4.2rem;
    background-size: 3rem 3rem;
    border-radius: 0.4rem;
  }

  @include mobile {
    left: unset;
    border-radius: 100%;
    bottom: 77px;
    right: 16px;
  }
}

