.shadow {
  position: relative;
  z-index: 0;
  max-width: 100%;

  &__body {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 200vh;
    transform: translateY(-55%);
    background: #000;
  }
}

.simplebar-track {
  right: 77px;

  @include mobile {
    display: none;
  }
}

.simplebar-track.simplebar-vertical {
  top: 0;
  bottom: 0;
  width: 6px;
  background-color: #d9d9d9;
}

.simplebar-placeholder {}

.simplebar-scrollbar::before {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  background-color: var(--focus-color);

  @include mobile {
    display: none;
  }
}

.plug {
